:root {
  --pink: #ff1a8c;
  --pink-light: #ffd6ea;
  --gray-soft: #555;
}
.altSection {
}
.sectionInner {
  max-width: 1100px;
  margin: 0 auto;
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  gap: 18px;
  align-items: center;
}
.garantiaInner {
  align-items: center;
  text-align: center;
}
.sectionTitle {
  font-family: var(--font-playfair), serif;
  color: var(--pink);
  font-size: 1.6rem;
  margin: 0;
}
.sectionIntro {
  color: var(--gray-soft);
  max-width: 820px;
}
.primaryButton {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(90deg, #ff6fbf 0%, var(--pink) 60%);
  color: #fff;
  padding: 12px 22px;
  border-radius: 999px;
  text-decoration: none;
  font-weight: 900;
  box-shadow: 0 18px 40px rgba(255, 46, 122, 0.14);
  text-align: center;
}
